* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* 平滑滾動 */
html {
  scroll-behavior: smooth;
}

/* 1. 全站背景漸層 */
body {
  font-family: "Segoe UI", sans-serif;
  background: linear-gradient(180deg, #0f172a 0%, #1e293b 100%);
  color: #f1f5f9;
  line-height: 1.6;
  min-height: 100vh;
  padding-top: 72px;
  /* 讓主體內容不被 header 蓋住 */
}

/* 2. header 與 footer */
header,
footer {
  background: #101729;
  box-shadow: 0 2px 16px rgba(0, 0, 0, 0.12);
}

header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 72px;
  background: #101729;
  box-shadow: 0 2px 16px rgba(0, 0, 0, 0.12);
  z-index: 100;
  display: flex;
  align-items: center;
}

footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 2.5rem 2.5rem 1.5rem 2.5rem;
  font-size: 0.95rem;
  color: #94a3b8;
  margin-top: 2rem;
  box-shadow: 0 -2px 16px rgba(0, 0, 0, 0.1);
}

footer h2 {
  color: #f1f5f9;
  margin-bottom: 0.5rem;
}

/* 3. 標題字體 */
header h1 {
  font-size: 3rem;
  margin-bottom: 0.7rem;
  font-weight: 700;
  letter-spacing: 1px;
}

h2 {
  text-align: center;
  font-size: 2.2rem;
  margin: 3rem 0 1.5rem;
  font-weight: 700;
  letter-spacing: 1px;
}

/* 4. explore 數據卡 */
#explore ul {
  display: flex;
  justify-content: center;
  gap: 2rem;
  margin-top: 1rem;
  flex-wrap: wrap;
  list-style: none;
  text-align: center;
}

#explore li {
  background: transparent;
  color: #f1f5f9;
  font-size: 1.1rem;
}

#explore li strong {
  font-size: 2rem;
  color: rgb(245 158 11 / var(--tw-bg-opacity, 1));
  display: block;
}

/* 5. 熱門電影卡片容器 */
#popular-movies {
  display: flex;
  flex-wrap: wrap;
  gap: 2rem;
  justify-content: center;
  margin-bottom: 2rem;
}

/* 6. 熱門電影卡片樣式與主題色 */
.movie {
  border-radius: 16px;
  padding: 1.8rem 1.5rem 1.5rem 1.5rem;
  width: 300px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.18);
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  position: relative;
  transition: transform 0.15s;
  background: #1e293b;
}

.movie:hover {
  transform: translateY(-6px) scale(1.03);
  box-shadow: 0 16px 40px rgba(0, 0, 0, 0.22);
}

.movie .icon {
  width: 60px;
  height: 60px;
  border-radius: 12px;
  margin-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 2.2rem;
  color: #f1f5f9;
}

.movie.yellow {
  background: #facc15;
  color: #1e293b;
}

.movie.blue {
  background: #818cf8;
  color: #1e293b;
}

.movie.red {
  background: #f87171;
  color: #1e293b;
}

.movie.green {
  background: #4ade80;
  color: #1e293b;
}

/* 7. 熱門電影評分 badge */
.movie .rating-badge {
  position: absolute;
  top: 1rem;
  right: 1rem;
  color: #ffffff;
  background: rgb(245 158 11 / var(--tw-bg-opacity, 1));
  padding: 0.2rem 0.7rem;
  border-radius: 8px;
  font-size: 0.95rem;
  font-weight: 900;
}

/* 8. 即將上映卡片 */
#coming-soon-movies {
  display: flex;
  flex-wrap: wrap;
  gap: 2.8rem;
  justify-content: center;
  margin-bottom: 2rem;
  padding: 2rem 0.3rem;
}

.movie.upcoming {
  background: #232b3b;
  color: #f1f5f9;
  border-radius: 8px;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.18);
  position: relative;
  padding: 1rem 1.5rem;
  width: 100%;
  min-width: 0px;
  max-width: 608px;
  margin: 0;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.movie.upcoming h3 {
  font-size: 20px;
  font-weight: 700;
  margin-bottom: 1rem;
  margin-top: 1rem;
  color: #fff;
}

.movie.upcoming .badge {
  position: absolute;
  top: 1.8rem;
  right: 1.5rem;
  background: #ef4444;
  color: #fff;
  padding: 2.5px 8px;
  border-radius: 4px;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1px;
}

.movie.upcoming .meta {
  color: #cbd5e1;
  font-size: 1rem;
  margin-bottom: 1.1rem;
  line-height: 1.6;
}

.movie.upcoming p {
  color: #f1f5f9;
  margin-bottom: 1.7rem;
  font-size: 1rem;
  line-height: 1.7;
}

.movie.upcoming .remind-row {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-top: auto;
  width: 100%;
  gap: 1rem;
  flex-direction: row;
  flex-wrap: nowrap;
  white-space: nowrap;
}

.movie.upcoming button {
  background: transparent;
  color: rgb(245, 158, 11);
  border: 1px solid rgb(245, 158, 11);
  padding: 0.6rem 0rem;
  font-size: 16px;
  font-weight: 400;
  border-radius: 4px;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, border-color 0.2s;
  box-shadow: none;
  white-space: nowrap;
  min-width: 120px;
  max-width: 100%;
  margin-top: 0;
  margin-bottom: 0;
  display: inline-block;
}

.movie.upcoming button:hover {
  background: rgb(245, 158, 11);
  color: #ffffff;
  border-color: rgb(245, 158, 11);
}

.movie.upcoming .expect {
  color: #cbd5e1;
  font-size: 1.08rem;
  display: flex;
  align-items: center;
}

.movie.upcoming .stars {
  display: flex;
  align-items: center;
  gap: 0.18em; /* 星星間距 */
  font-size: 1.15rem;
  margin-left: 0; /* 移除多餘左邊距 */
  letter-spacing: 0;
}

.movie.upcoming .stars .star,
.movie.upcoming .stars .star.gray {
  display: flex;
  align-items: center;
  padding: 0;
  margin: 0;
}

.movie.upcoming .stars .star svg {
  width: 1.25em;
  height: 1.25em;
  fill: #facc15;
  display: block;
}

.movie.upcoming .stars .star.gray svg {
  fill: #64748b;
}

/* 9. 按鈕樣式 */
#search button {
  background: rgb(245 158 11 / var(--tw-bg-opacity, 1));
  color: #ffffff;
  border: none;
  padding: 0.7rem 1.8rem;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 999px;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
  box-shadow: 0 2px 8px rgba(250, 204, 21, 0.08);
}

.movie button {
  background: rgb(245 158 11 / var(--tw-bg-opacity, 1));
  color: #ffffff;
  border: none;
  padding: 0.7rem 1.8rem;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 8px;
  cursor: pointer;
  transition: background 0.2s, color 0.2s;
  box-shadow: 0 2px 8px rgba(250, 204, 21, 0.08);
  margin-top: 1rem;
}

.movie button:hover,
#search button:hover {
  background: rgb(217 119 6 / var(--tw-bg-opacity, 1));
  color: #ffffff;
  box-shadow: 0 4px 16px rgba(250, 204, 21, 0.18);
}

/* 10. 搜尋區塊與標籤 */
#search {
  background: transparent;
  border-radius: 0;
  padding: 0;
  margin: 3.5rem auto 0 auto;
  max-width: 100vw;
  text-align: center;
  box-shadow: none;
}

#search h2 {
  text-align: center;
  font-size: 2.25rem;
  margin: 1.5rem 0;
  font-weight: 800;
  letter-spacing: 2px;
  color: #fff;
}

.search-box {
  background: #374151;
  border-radius: 8px;
  padding: 1.8rem 1.8rem;
  max-width: 768px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0rem;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.13);
}

.search-input-wrap {
  display: flex;
  align-items: center;
  background: #232b3b;
  border: 2px solid rgb(245 158 11 / var(--tw-border-opacity, 1));
  border-radius: 60px;
  padding: 0.1rem 1rem;
  width: 100%;
  max-width: 100%;
  position: relative;
  margin-bottom: 1.5rem;
}

.search-input-wrap:focus-within {
  transition: all 0.3s ease;
  box-shadow: 0 0 15px rgba(245, 158, 11, 0.5);
}

.search-input-wrap input::placeholder {
  color: rgb(156 163 175 / var(--tw-text-opacity, 1));
  opacity: 1;
  font-weight: 500;
}

.search-icon {
  display: flex;
  align-items: center;
  margin-right: 1rem;
  color: rgb(245 158 11 / var(--tw-border-opacity, 1));
}

.search-icon svg {
  width: 20px;
}

.search-input-wrap input {
  background: transparent;
  border: none;
  outline: none;
  color: #f1f5f9;
  font-size: 16px;
  flex: 1;
  padding: 0.7rem 0;
  font-weight: normal;
  letter-spacing: 1px;
}

.tags {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  gap: 0;
  width: 100%;
  max-width: 100%;
  margin-bottom: 0;
}

.tags span {
  background: #232b3b;
  width: 25%; /* 四個平均分配 */
  min-width: 160px;
  max-width: 168px;
  border-radius: 999px;
  cursor: pointer;
  color: #fff;
  font-weight: 600;
  transition: background 0.3s, color 0.3s;
  box-shadow: none;
  border: none;
  text-align: center;
  margin-bottom: 0;
  display: inline-block;
  margin-right: 0;
  font-size: 1rem;
  padding: 0.5rem 1.2rem;
}

.tags span:not(:last-child) {
  margin-right: 0;
}

.tags span.active,
.tags span:hover {
  background: rgb(245 158 11 / var(--tw-bg-opacity, 1));
  color: #fff;
  box-shadow: 0 4px 24px #facc1533;
}

/* 響應式：小螢幕自動換行 */
@media (max-width: 900px) {
  .tags {
    flex-wrap: wrap;
    gap: 1rem;
    justify-content: center;
  }
  .tags span {
    width: auto;
    min-width: 140px;
    font-size: 1.2rem;
    padding: 0.7rem 1.5rem;
    margin-bottom: 0.5rem;
  }
}
.search-btn {
  background: #facc15;
  color: #fff;
  border: none;
  padding: 1.2rem 4.5rem;
  font-size: 2.2rem;
  font-weight: bold;
  border-radius: 999px;
  cursor: pointer;
  margin-top: 1.5rem;
  box-shadow: 0 8px 32px #facc1533;
  transition: background 0.2s, color 0.2s, transform 0.18s;
  letter-spacing: 2px;
  display: inline-block;
}

.search-btn:hover {
  background: #fde047;
  color: #1e293b;
  transform: scale(1.06);
}

/* 響應式 */
@media (max-width: 900px) {
  .search-box {
    padding: 2rem 0.5rem 2rem 0.5rem;
  }
  .search-input-wrap input,
  .tags span,
  .search-btn {
    font-size: 16px;
  }
  .tags span {
    padding: 0.7rem 1.5rem;
  }
  .search-btn {
    padding: 1rem 2.5rem;
  }
}

@media (max-width: 600px) {
  #search h2 {
    font-size: 2rem;
    margin: 1.2rem 0 1.2rem 0;
  }
  .search-box {
    padding: 1.5rem 1rem;
  }
  .search-input-wrap {
    padding: 0.3rem 0.7rem;
  }
  .tags {
    gap: 0.7rem;
  }
  .tags span {
    font-size: 1rem;
    padding: 0.5rem 1.2rem;
  }
  .search-btn {
    font-size: 1.1rem;
    padding: 0.7rem 1.5rem;
    margin-top: 1rem;
  }
}

#about {
  padding: 1.5rem 0.5rem;
}

/* 11. 關於我們區塊 */
.about-box {
  background-color: rgb(51 65 85 / var(--tw-bg-opacity, 1));
  border-radius: 0.5rem;
  padding: 1.5rem;
  margin: 1rem auto 2rem auto;
  max-width: 48rem;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.1);
}

.about-card {
  padding: 0 0 1.5rem 0;
}

.about-card h3 {
  font-size: 1.5rem;
  line-height: 2rem;
}

#about p,
#about li {
  color: #cbd5e1;
  padding: 0.5rem 0 0 0;
}

#about ul {
  list-style: disc;
  margin-top: 1rem;
}

.social-icons {
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-top: 1rem;
}

.social-icons a {
  background: #334155;
  color: #f1f5f9;
  padding: 0.7rem 1.2rem;
  border-radius: 8px;
  text-decoration: none;
  font-size: 1rem;
  transition: background 0.3s, color 0.3s;
  margin: 0 0.2rem;
}

.social-icons a:hover {
  background: #facc15;
  color: #1e293b;
  box-shadow: 0 2px 8px rgba(250, 204, 21, 0.1);
}

.about-list {
  list-style: none;
  padding-left: 0;
  margin: 1.5rem 0;
}

.about-list li {
  display: flex;
  align-items: center;
  font-size: 16px;
  color: #cbd5e1;
}

.about-list .li-icon {
  display: flex;
  align-items: center;
  margin-right: 0.5rem;
  flex-shrink: 0;
  margin-top: 0;
  /* 移除多餘的上邊距 */
}

.about-list .li-icon svg {
  width: 1.5rem;
  height: 1.5rem;
  min-width: 1.5rem;
  min-height: 1.5rem;
  color: #eaa13b;
  display: block;
}

/* Header 內部橫向分散 */
.header-inner {
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 100%;
  height: 100%;
  padding: 0 1rem;
}

/* LOGO 樣式 */
.logo {
  display: flex;
  align-items: center;
  font-size: 1.3rem;
  font-weight: bold;
  color: #f1f5f9;
  letter-spacing: 1px;
  max-width: 800px;
}

.logo-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-right: 0.5rem;
  /* 保持與文字垂直置中 */
}

.logo-icon svg {
  width: 2.2rem;
  height: 2.2rem;
  color: #eaa13b;
  display: block;
}

.logo-text {
  font-size: 1.45rem;
  font-weight: 700;
  color: #fff;
  letter-spacing: 1px;
  line-height: 1;
}

footer .logo-text {
  font-size: 1.2rem;
}

footer .logo-icon svg {
  width: 1.5rem;
  height: 1.5rem;
}

footer .logo-icon {
  margin-right: 0.3rem;
}

.footer-right {
  max-width: 120px;
}

/* 導覽列樣式 */
nav ul {
  display: flex;
  gap: 2rem;
  list-style: none;
}

nav ul li a {
  color: #f1f5f9;
  text-decoration: none;
  font-size: 1.08rem;
  position: relative;
  /* 讓 ::after 定位 */
  padding-bottom: 4px;
  transition: color 0.2s;
}

nav ul li a::after {
  content: "";
  position: absolute;
  left: 0;
  bottom: 0;
  width: 0%;
  height: 2.5px;
  background: #eaa13b;
  transition: width 0.3s;
  border-radius: 2px;
}

nav ul li a:hover,
nav ul li a.active {
  color: #ffffff;
  background: rgba(250, 204, 21, 0.06);
  border-radius: 4px 4px 0 0;
}

nav ul li a:hover::after,
nav ul li a.active::after {
  width: 100%;
}

/* 漢堡選單按鈕 */
.menu-toggle {
  display: none;
  flex-direction: column;
  justify-content: center;
  gap: 5px;
  width: 36px;
  height: 36px;
  background: none;
  border: none;
  cursor: pointer;
  margin-left: 1rem;
}

.menu-toggle span {
  display: block;
  height: 4px;
  width: 100%;
  background: #f1f5f9;
  border-radius: 2px;
  transition: all 0.3s;
}

/* 手機版 header */
@media (max-width: 768px) {
  .header-inner {
    padding: 0 1rem;
  }

  nav {
    display: flex;
    align-items: center;
  }

  nav ul {
    display: none;
    position: absolute;
    top: 72px;
    right: 0;
    left: 0;
    background: #101729;
    flex-direction: column;
    gap: 0;
    z-index: 99;
    box-shadow: 0 4px 16px rgba(0, 0, 0, 0.12);
    margin: 0;
    padding: 1rem 0;
  }

  nav ul.show {
    display: flex;
  }

  nav ul li {
    border-bottom: 0px solid #334155;
    text-align: center;
    padding: 0.75rem 0;
  }

  .menu-toggle {
    display: flex !important;
    z-index: 100;
    position: relative;
  }
}

@media (max-width: 1024px) {
  .movie {
    width: 90vw;
    max-width: 350px;
  }

  #popular-movies,
  #coming-soon-movies {
    gap: 1.2rem;
    flex-direction: column;
    align-items: center;
  }

  .movie {
    width: 95vw;
    max-width: 98vw;
  }

  #search {
    padding: 1.5rem 0.5rem;
  }
}

@media (max-width: 500px) {
  .movie.upcoming .remind-row {
    gap: 0.7rem;
  }

  .movie.upcoming button {
    width: 100px;
    min-width: 0;
    text-align: center;
  }
}

/* Hero 區塊 */
.hero {
  text-align: center;
  margin: 0 auto 2.5rem auto;
  padding: 3.5rem 1rem 2rem 1rem;
}

.hero h1 {
  font-size: 3.75rem;
  font-weight: 700;
  color: #f1f5f9;
  margin-bottom: 0.7rem;
  letter-spacing: 1px;
}

.hero .subtitle {
  font-size: 1.25rem;
  color: #cbd5e1;
  margin-bottom: 2rem;
}

.hero-btn {
  background: rgb(245 158 11 / var(--tw-bg-opacity, 1));
  color: #ffffff;
  border: none;
  padding: 1rem 2.5rem;
  font-size: 1.15rem;
  font-weight: bold;
  border-radius: 2rem;
  cursor: pointer;
  transition: background 0.2s, color 0.2s, transform 0.18s;
  box-shadow: 0 2px 8px rgba(250, 204, 21, 0.1);
  animation: pulse 1s infinite;
}

.hero-btn:hover {
  background: rgb(217 119 6 / var(--tw-bg-opacity, 1));
  color: #ffffff;
  transform: scale(1.08);
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }

  50% {
    transform: scale(1.08);
  }

  100% {
    transform: scale(1);
  }
}

.explore-cards {
  display: flex;
  flex-direction: column;
  gap: 2rem;
  max-width: 700px;
  margin: 0 auto 2.5rem auto;
}

@media (min-width: 600px) {
  .explore-cards {
    flex-direction: row;
  }
}

@media (min-width: 760px) {
  .movie.upcoming {
    min-width: 320px;
    max-width: 480px;
  }

  #coming-soon-movies {
    gap: 1.5rem;
  }
}

.explore-card {
  background: #192133;
  border-radius: 1.2rem;
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.1);
  padding: 2.2rem 1.5rem 1.5rem 1.5rem;
  text-align: center;
  flex: 1 1 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.explore-number {
  color: rgb(245 158 11 / var(--tw-bg-opacity, 1));
  font-size: 2.5rem;
  font-weight: 700;
  margin-bottom: 0.5rem;
  letter-spacing: 1px;
}

.explore-label {
  color: #cbd5e1;
  font-size: 1.1rem;
  margin-bottom: 1rem;
}
